    mounted():void {
        window.addEventListener("scroll", this.handleScrollHeaderToggle, true);
    }

    handleScrollHeaderToggle():void {
        let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
        if (scrollTop > 60) {
            (this.$refs.headerTypeList as HTMLElement).style.position = "fixed";
            (this.$refs.headerTypeList as HTMLElement).style.top = "0";
        } else {
            (this.$refs.headerTypeList as HTMLElement).style.position = "relative";
        }
    }
